.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 750px;
  }
  .maincard {
      position: absolute;
      width: 350px;
      height: 350px;
      top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .thecard {
      position: absolute;
      width: 100%;
      height: 100%;
      transform-style: preserve-3d;
      transition: all 0.8s ease
  }
  .thecard:hover {
      transform: rotateY(180deg)
  }
  .thefront {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: hidden;
      background: #A5C7D3;
      color: #fff;
      border-radius: 10px;
      cursor: pointer;
      border: 1px solid #eee;
      box-shadow: 5px 6px 6px 2px #E9ECEF;
      padding: 85px 75px
  }
  .theback {
      position: absolute;
      width: 100%;
      height: 100%;
      cursor: pointer;
      background-color: hidden;
      background: #A5C7D3;
      color: #fff;
      border-radius: 10px;
      transform: rotateY(180deg);
      border: 1px solid #eee;
      box-shadow: 5px 6px 6px 2px #E9ECEF;
      padding: 25px
  }
  .user_name {
      color: #eee;
      font-size: 10px;
      margin-top: 0px
  }
  .about {
      font-size: 12px
  }
  .follow-button,
  .follow-button:active,
  .follow-button:visited,
  .follow-button:focus {
      background-color: #A5C7D3 !important;
      border-color: #A5C7D3 !important;
      color: #fff !important;
      box-shadow: none
  }
  .follow-button:hover {
      background-color: #A5C7D3 !important;
      border-color: #A5C7D3 !important
  }
  .links {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      flex-wrap: nowrap;
      margin-top: 75px;
  }
  .containerU {
      height: 350px;
  }
  .seller {
      min-height: 350px;
  }
  .myLink {
      text-align: center;
  }
  .centerP {
      display: block;
      margin-left: auto;
      margin-right: auto;
      width: 15%;
    }